.profile-container {
  width: 40rem;
  height: 25rem;
  background-color: var(--accent10);
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.profile-container::after {
  content: "";
  position: relative;
  top: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
  width: 5px;
  height: 85%;
  border-radius: 25px;
  background-color: var(--text15);
}

.profile-infos,
.profile-invites {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin: 0.75rem 1.25rem;
}

.profile-username {
  display: flex;
  align-items: center;
  justify-content: left;
}

.profile-username > img {
  width: 75px;
  height: 75px;
  aspect-ratio: 1/2;
}

.profile-disconnect {
  border: none;
  background-color: var(--primary65);
  border-radius: 25px;
  padding: 0.3rem 0.9rem;
  color: var(--text);
  font-weight: 600;
  transition: all 0.5s ease;
  width: min-content;
}

.profile-disconnect:hover {
  cursor: pointer;
  background-color: var(--secondary50);
}

.profile-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0.5rem;
}

.profile-buttons > button {
  border: none;
  background-color: var(--primary65);
  border-radius: 25px;
  padding: 0.3rem 0.6rem;
  color: var(--text);
  font-weight: 600;
  transition: all 0.5s ease;
}

.profile-buttons > button:hover {
  cursor: pointer;
  background-color: var(--secondary50);
}

.profile-invite {
  display: flex;
  align-items: end;
  flex-direction: column;
  gap: 0.75rem;
}

.profile-inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  row-gap: 0.5rem;
  column-gap: 1.6rem;
}

.profile-invite > p,
.profile-infos > p,
.profile-pairs-buttons > p {
  min-height: 25px;
  margin: 0;
  color: var(--text65);
}

.profile-player-input > input {
  border: none;
  background-color: var(--primary65);
  border-radius: 25px;
  padding: 0.3rem 0.6rem;
  color: var(--text);
  font-weight: 600;
  transition: all 0.5s ease;
  width: 6rem;
}

.profile-player-input > div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0.5rem;
  color: var(--text65);
  line-height: 0rem;
}

.profile-invites > h5 {
  color: var(--text65);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.profile-invitesList {
  position: relative;
  height: 18rem;
  max-height: 18rem;
}

.profile-invitesList > p {
  max-width: 20rem;
  display: flex;
  flex-wrap: wrap;
}

.profile-invites > h5 > svg:hover,
.profile-invitesList > p > span:hover,
.profile-invitesList > p > svg:hover,
.profile-username > h5:hover {
  cursor: pointer;
}

.profile-invitesList > p {
  margin: 0;
  color: var(--text95);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.2rem;
  font-size: 13px;
}

.profile-invitesList > p > i {
  text-decoration: underline 1px solid;
}

.profile-pairs-buttons {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}

@media (max-width: 580px) {
  .profile-container {
    width: 17rem;
    height: 60%;
    background-color: var(--accent10);
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 20%;
    overflow-y: scroll;
  }

  .profile-container::after {
    display: none;
  }

  .profile-infos,
  .profile-invites {
    width: 80%;
    height: 80%;
  }

  .profile-invitesList {
    max-height: 10rem;
    overflow-y: scroll;
  }

  .profile-player-input > input {
    width: 4.5rem;
  }
}

.profile-buttons-joining {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.profile-leaderboard {
  position: relative;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

@media (orientation: landscape) {
  .profile-container {
    margin-top: 3.5rem;
  }
}
